.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@font-face {
    font-family: 'whateverItTakes';
    font-weight: normal;
    font-style: normal;
    src: local('whateverItTakes'), url(/assets/fonts/whateverItTakes.tff) format('tff');
}

@font-face {
    font-family: 'queens';
    font-weight: normal;
    font-style: normal;
    src: local('queens'), url(/assets/fonts/queens.tff) format('tff');
}
